.cruise-card__cover {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 100;

    // display: none;

    width: 100%;
    height: 100%;

    background-color: $color-tangaroa;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    transition: height $trans-default;
}

.cruise-card__heading {
    @include unselectable;

    margin: auto 30px 22px;
    padding: 0;

    color: $color-default-white;
    font: 500 18px/150% $ff-montserrat;

    @include vp-1023 {
        margin: auto 35px 26px;
    }

    @include vp-767 {
        margin: auto 25px 17px;
    }
}

.cruise-card__info {
    width: 100%;
    height: 100%;
    padding: 30px 24px 33px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    word-break: break-word;

    background-color: $color-tangaroa;
    color: $color-default-white;

    @include vp-1023 {
      padding: 30px 35px 33px;
      overflow-y: initial;
    }

    @include vp-767 {
      padding: 30px 25px 32px;
    }
}

.cruise-card__cruise-name {
    @include unselectable;

    margin: 0 0 13px 0;
    padding: 0;

    font: 500 18px/150% $ff-montserrat;
}

.cruise-card__details {
    margin: 0 0 22px;
    padding: 0;

    @include vp-767 {
      margin-bottom: 23px;
    }
}

.cruise-card__term {
    @include unselectable;

    margin-bottom: 4px;

    font: 500 14px/140% $ff-montserrat;
}
.cruise-card__description {
    @include unselectable;

    margin: 0;

    font: 400 14px/140% $ff-montserrat;

    &:not(:last-child) {
        margin-bottom: 11px;
    }
}

.cruise-card__button {
    margin-top: auto;
}
